module.exports = "import { Component } from '@angular/core';\r\n\r\n@Component({\r\n  selector: 'nz-demo-steps-step-next',\r\n  template: `\r\n    <nz-steps [nzCurrent]=\"current\">\r\n      <nz-step nzTitle=\"Finished\"></nz-step>\r\n      <nz-step nzTitle=\"In Progress\"></nz-step>\r\n      <nz-step nzTitle=\"Waiting\"></nz-step>\r\n    </nz-steps>\r\n\r\n    <div class=\"steps-content\">{{ index }}</div>\r\n    <div class=\"steps-action\">\r\n      <button nz-button nzType=\"default\" (click)=\"pre()\" *ngIf=\"current > 0\">\r\n        <span>Previous</span>\r\n      </button>\r\n      <button nz-button nzType=\"default\" (click)=\"next()\" *ngIf=\"current < 2\">\r\n        <span>Next</span>\r\n      </button>\r\n      <button nz-button nzType=\"primary\" (click)=\"done()\" *ngIf=\"current === 2\">\r\n        <span>Done</span>\r\n      </button>\r\n    </div>\r\n  `,\r\n  styles  : [\r\n      `\r\n      .steps-content {\r\n        margin-top: 16px;\r\n        border: 1px dashed #e9e9e9;\r\n        border-radius: 6px;\r\n        background-color: #fafafa;\r\n        min-height: 200px;\r\n        text-align: center;\r\n        padding-top: 80px;\r\n      }\r\n\r\n      .steps-action {\r\n        margin-top: 24px;\r\n      }\r\n\r\n      button {\r\n        margin-right: 8px;\r\n      }\r\n    `\r\n  ]\r\n})\r\nexport class NzDemoStepsStepNextComponent {\r\n  current = 0;\r\n\r\n  index = 'First-content';\r\n\r\n  pre(): void {\r\n    this.current -= 1;\r\n    this.changeContent();\r\n  }\r\n\r\n  next(): void {\r\n    this.current += 1;\r\n    this.changeContent();\r\n  }\r\n\r\n  done(): void {\r\n    console.log('done');\r\n  }\r\n\r\n  changeContent(): void {\r\n    switch (this.current) {\r\n      case 0: {\r\n        this.index = 'First-content';\r\n        break;\r\n      }\r\n      case 1: {\r\n        this.index = 'Second-content';\r\n        break;\r\n      }\r\n      case 2: {\r\n        this.index = 'third-content';\r\n        break;\r\n      }\r\n      default: {\r\n        this.index = 'error';\r\n      }\r\n    }\r\n  }\r\n\r\n  constructor() {\r\n  }\r\n}\r\n"